"use client"

import React, { useState, useEffect } from 'react';
import { User, Calendar, Clock, Users, UserCheck, Heart, FileText } from 'lucide-react';

interface FormData {
  assignmentId: string;
  date: string;
  time: string;
  patientId: string;
  doctorId: string;
  volunteerId: string;
  nurseId: string;
  description: string;
}

interface FormErrors {
  [key: string]: string;
}

const AssignmentForm = () => {
  const [formData, setFormData] = useState<FormData>({
    assignmentId: '',
    date: '',
    time: '',
    patientId: '',
    doctorId: '',
    volunteerId: '',
    nurseId: '',
    description: ''
  });

  const [errors, setErrors] = useState<FormErrors>({});

  // Mock data - replace with actual API calls
  const [patients] = useState([
    { id: 'P001', name: 'John Doe', diagnosis: 'Hypertension', age: 45, phone: '+1234567890', category: 'Chronic' },
    { id: 'P002', name: 'Jane Smith', diagnosis: 'Diabetes Type 2', age: 52, phone: '+1234567891', category: 'Chronic' },
    { id: 'P003', name: 'Mike Johnson', diagnosis: 'Acute Bronchitis', age: 38, phone: '+1234567892', category: 'Acute' },
    { id: 'P004', name: 'Sarah Wilson', diagnosis: 'Migraine', age: 29, phone: '+1234567893', category: 'Neurological' },
    { id: 'P005', name: 'David Brown', diagnosis: 'Coronary Artery Disease', age: 61, phone: '+1234567894', category: 'Cardiac' }
  ]);

  const [doctors] = useState([
    { id: 'D001', name: 'Dr. Emily Chen', specialization: 'Cardiology', department: 'Cardiac Care', phone: '+1234567895' },
    { id: 'D002', name: 'Dr. Robert Martinez', specialization: 'Internal Medicine', department: 'General Medicine', phone: '+1234567896' },
    { id: 'D003', name: 'Dr. Lisa Anderson', specialization: 'Neurology', department: 'Neuroscience', phone: '+1234567897' },
    { id: 'D004', name: 'Dr. James Taylor', specialization: 'Pulmonology', department: 'Respiratory Care', phone: '+1234567898' },
    { id: 'D005', name: 'Dr. Maria Rodriguez', specialization: 'Endocrinology', department: 'Diabetes Care', phone: '+1234567899' }
  ]);

  const [volunteers] = useState([
    { id: 'V001', name: 'Alex Thompson', type: 'Patient Support', status: 'Active', phone: '+1234567800' },
    { id: 'V002', name: 'Emma Davis', type: 'Administrative', status: 'Active', phone: '+1234567801' },
    { id: 'V003', name: 'Ryan Miller', type: 'Transport', status: 'Available', phone: '+1234567802' },
    { id: 'V004', name: 'Sophie Clark', type: 'Counseling', status: 'Active', phone: '+1234567803' },
    { id: 'V005', name: 'Lucas White', type: 'Technical Support', status: 'Available', phone: '+1234567804' }
  ]);

  const [nurses] = useState([
    { id: 'N001', name: 'Nurse Jennifer Lee', specialization: 'ICU', department: 'Critical Care', phone: '+1234567805' },
    { id: 'N002', name: 'Nurse Michael Brown', specialization: 'Emergency', department: 'Emergency Medicine', phone: '+1234567806' },
    { id: 'N003', name: 'Nurse Anna Garcia', specialization: 'Pediatrics', department: 'Children Care', phone: '+1234567807' },
    { id: 'N004', name: 'Nurse Thomas Wilson', specialization: 'Surgical', department: 'Surgery', phone: '+1234567808' },
    { id: 'N005', name: 'Nurse Rachel Adams', specialization: 'Cardiac', department: 'Cardiac Care', phone: '+1234567809' }
  ]);

  // Initialize form data
  useEffect(() => {
    const generateId = () => {
      const timestamp = Date.now().toString(36);
      const random = Math.random().toString(36).substr(2, 5);
      return `ASN${timestamp}${random}`.toUpperCase();
    };

    const getCurrentDateTime = () => {
      const now = new Date();
      const date = now.toISOString().split('T')[0];
      const time = now.toTimeString().slice(0, 5);
      return { date, time };
    };

    const { date, time } = getCurrentDateTime();
    setFormData(prev => ({
      ...prev,
      assignmentId: generateId(),
      date: date,
      time: time
    }));
  }, []);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};
    
    if (!formData.patientId) newErrors.patientId = 'Please select a patient';
    if (!formData.doctorId) newErrors.doctorId = 'Please select a doctor';
    if (!formData.volunteerId) newErrors.volunteerId = 'Please select a volunteer';
    if (!formData.nurseId) newErrors.nurseId = 'Please select a nurse';
    if (!formData.description.trim()) newErrors.description = 'Please provide a description';
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    
    // Update time to current time when submitting
    const currentTime = new Date().toTimeString().slice(0, 5);
    const submissionData = {
      ...formData,
      time: currentTime
    };
    
    if (validateForm()) {
      console.log('Assignment Data:', submissionData);
      alert('Assignment created successfully!');
      // Here you would typically send data to your backend
    }
  };

  const inputClasses = (fieldName: keyof FormData): string => `
    w-full p-2 border-2 rounded-lg text-red-900 font-medium transition-all duration-300 text-sm
    ${errors[fieldName] 
      ? 'border-red-400 bg-red-50 focus:border-red-500 focus:ring-red-200' 
      : 'border-red-200 bg-white focus:border-red-400 focus:ring-red-100'
    }
    focus:outline-none focus:ring-2
  `;

  const selectClasses = (fieldName: keyof FormData): string => `
    ${inputClasses(fieldName)} cursor-pointer
    appearance-none bg-white
  `;

  return (
    <div className="min-h-screen w-full bg-gradient-to-br from-red-50 to-red-100 px-4 py-4">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="bg-gradient-to-r from-red-900 to-red-700 text-white p-4 text-center rounded-t-2xl">
          <div className="flex justify-center mb-2">
            <Users className="w-12 h-12 text-red-200" />
          </div>
          <h1 className="text-2xl font-light mb-1">
            Healthcare Assignment Form
          </h1>
          <p className="text-sm text-red-200">
            Assign patients to healthcare professionals
          </p>
        </div>

        {/* Form */}
        <div className="bg-white p-4 rounded-b-2xl shadow-lg">
          <div className="space-y-4">
            
            {/* Assignment Details Section */}
            <div className="bg-gray-50 p-6 rounded-2xl border-l-4 border-red-600">
              <div className="flex items-center mb-6">
                <FileText className="w-6 h-6 text-red-600 mr-3" />
                <h3 className="text-xl font-semibold text-red-900">Assignment Details</h3>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div>
                  <label className="block text-sm font-semibold text-red-900 mb-2 uppercase tracking-wide">
                    Assignment ID <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    name="assignmentId"
                    value={formData.assignmentId}
                    className={`${inputClasses('assignmentId')} bg-gray-100 cursor-not-allowed`}
                    placeholder="Auto-generated ID"
                    readOnly
                  />
                </div>

                <div>
                  <label className="block text-sm font-semibold text-red-900 mb-2 uppercase tracking-wide">
                    Date <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                    <input
                      type="date"
                      name="date"
                      value={formData.date}
                      onChange={handleInputChange}
                      className={`${inputClasses('date')} pl-12`}
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-red-900 mb-2 uppercase tracking-wide">
                    Time <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <Clock className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                    <input
                      type="time"
                      name="time"
                      value={formData.time}
                      onChange={handleInputChange}
                      className={`${inputClasses('time')} pl-12`}
                    />
                  </div>
                </div>
              </div>
            </div>

            {/* Patient Selection */}
            <div className="bg-blue-50 p-6 rounded-2xl border-l-4 border-blue-600">
              <div className="flex items-center mb-6">
                <User className="w-6 h-6 text-blue-600 mr-3" />
                <h3 className="text-xl font-semibold text-blue-900">Patient Assignment</h3>
              </div>

              <div>
                <label className="block text-sm font-semibold text-blue-900 mb-2 uppercase tracking-wide">
                  Select Patient <span className="text-red-500">*</span>
                </label>
                <select
                  name="patientId"
                  value={formData.patientId}
                  onChange={handleInputChange}
                  className={selectClasses('patientId').replace('red', 'blue')}
                >
                  <option value="">Choose a patient...</option>
                  {patients.map(patient => (
                    <option key={patient.id} value={patient.id}>
                      {patient.id} | {patient.name} | {patient.diagnosis} | Age: {patient.age} | {patient.phone} | Category: {patient.category}
                    </option>
                  ))}
                </select>
                {errors.patientId && <p className="text-red-500 text-sm mt-1">{errors.patientId}</p>}
              </div>
            </div>

            {/* Doctor Selection */}
            <div className="bg-green-50 p-6 rounded-2xl border-l-4 border-green-600">
              <div className="flex items-center mb-6">
                <UserCheck className="w-6 h-6 text-green-600 mr-3" />
                <h3 className="text-xl font-semibold text-green-900">Doctor Assignment</h3>
              </div>

              <div>
                <label className="block text-sm font-semibold text-green-900 mb-2 uppercase tracking-wide">
                  Select Doctor <span className="text-red-500">*</span>
                </label>
                <select
                  name="doctorId"
                  value={formData.doctorId}
                  onChange={handleInputChange}
                  className={selectClasses('doctorId').replace('red', 'green')}
                >
                  <option value="">Choose a doctor...</option>
                  {doctors.map(doctor => (
                    <option key={doctor.id} value={doctor.id}>
                      {doctor.id} | {doctor.name} | {doctor.specialization} | {doctor.department} | {doctor.phone}
                    </option>
                  ))}
                </select>
                {errors.doctorId && <p className="text-red-500 text-sm mt-1">{errors.doctorId}</p>}
              </div>
            </div>

            {/* Volunteer Selection */}
            <div className="bg-purple-50 p-6 rounded-2xl border-l-4 border-purple-600">
              <div className="flex items-center mb-6">
                <Heart className="w-6 h-6 text-purple-600 mr-3" />
                <h3 className="text-xl font-semibold text-purple-900">Volunteer Assignment</h3>
              </div>

              <div>
                <label className="block text-sm font-semibold text-purple-900 mb-2 uppercase tracking-wide">
                  Select Volunteer <span className="text-red-500">*</span>
                </label>
                <select
                  name="volunteerId"
                  value={formData.volunteerId}
                  onChange={handleInputChange}
                  className={selectClasses('volunteerId').replace('red', 'purple')}
                >
                  <option value="">Choose a volunteer...</option>
                  {volunteers.map(volunteer => (
                    <option key={volunteer.id} value={volunteer.id}>
                      {volunteer.id} | {volunteer.name} | {volunteer.type} | Status: {volunteer.status} | {volunteer.phone}
                    </option>
                  ))}
                </select>
                {errors.volunteerId && <p className="text-red-500 text-sm mt-1">{errors.volunteerId}</p>}
              </div>
            </div>

            {/* Nurse Selection */}
            <div className="bg-cyan-50 p-6 rounded-2xl border-l-4 border-cyan-600">
              <div className="flex items-center mb-6">
                <Users className="w-6 h-6 text-cyan-600 mr-3" />
                <h3 className="text-xl font-semibold text-cyan-900">Nurse Assignment</h3>
              </div>

              <div>
                <label className="block text-sm font-semibold text-cyan-900 mb-2 uppercase tracking-wide">
                  Select Nurse <span className="text-red-500">*</span>
                </label>
                <select
                  name="nurseId"
                  value={formData.nurseId}
                  onChange={handleInputChange}
                  className={selectClasses('nurseId').replace('red', 'cyan')}
                >
                  <option value="">Choose a nurse...</option>
                  {nurses.map(nurse => (
                    <option key={nurse.id} value={nurse.id}>
                      {nurse.id} | {nurse.name} | {nurse.specialization} | {nurse.department} | {nurse.phone}
                    </option>
                  ))}
                </select>
                {errors.nurseId && <p className="text-red-500 text-sm mt-1">{errors.nurseId}</p>}
              </div>
            </div>

            {/* Description */}
            <div className="bg-gray-50 p-6 rounded-2xl border-l-4 border-gray-600">
              <div className="flex items-center mb-6">
                <FileText className="w-6 h-6 text-gray-600 mr-3" />
                <h3 className="text-xl font-semibold text-gray-900">Assignment Description</h3>
              </div>

              <div>
                <label className="block text-sm font-semibold text-gray-900 mb-2 uppercase tracking-wide">
                  Description <span className="text-red-500">*</span>
                </label>
                <textarea
                  name="description"
                  value={formData.description}
                  onChange={handleInputChange}
                  rows={3}
                  className={`${inputClasses('description').replace('red', 'gray')} resize-none`}
                  placeholder="Provide detailed information about this assignment, including care instructions, special requirements, or any relevant notes..."
                />
                {errors.description && <p className="text-red-500 text-sm mt-1">{errors.description}</p>}
              </div>
            </div>

            {/* Submit Button */}
            <div className="flex justify-center pt-8">
              <button
                onClick={handleSubmit}
                className="bg-gradient-to-r from-red-600 to-red-800 hover:from-red-700 hover:to-red-900 
                         text-white font-bold py-4 px-12 rounded-2xl text-lg
                         transform transition-all duration-300 hover:scale-105 hover:shadow-lg
                         focus:outline-none focus:ring-4 focus:ring-red-300"
              >
                Create Assignment
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AssignmentForm;